
import { computed, defineComponent } from 'vue'

export default defineComponent({
  props: {
    value: {
      type: Boolean,
      default: true,
    },
    size: {
      type: String,
      default: '30px',
    },
    fontSize: {
      type: String,
      default: '48px',
    },
  },
  setup(props, { emit }) {
    const divStyle = computed(() => {
      const size = props.size
      return {
        width: size,
        height: size,
        fontSize: props.fontSize,
      }
    })

    const checkChange = () => {
      const value = !props.value
      emit('input', value)
    }

    return { divStyle, checkChange }
  },
})
