
import { defineComponent, ref } from 'vue'
import GameMenu from './GameMenu.vue'
import GameView from './GameView/index.vue'
export default defineComponent({
  components: { GameMenu, GameView },
  setup() {
    // 0  1 
    const status = ref(0)

    const startNewGame = () => {
      // console.log('start')
      status.value = 1
    }

    const readRecord = () => {
      status.value = 1
    }

    const gameOver = () => {
      status.value = 0
    }

    return {
      status,
      startNewGame,
      readRecord,
      gameOver,
    }
  },
})
