
import { computed, defineComponent, ref, watchEffect } from 'vue'
// const bgImage = require('./bg.jpg')
// 
// const image = new Image()
// image.src = bgImage
// image.onload = () => {
//   console.log('image loaded')
// }

export default defineComponent({
  props: {
    width: {
      type: String,
      default: '350px',
    },
    height: {
      type: String,
      default: '500px',
    },
    visible: {
      type: Boolean,
      defalut: true,
    },
    title: {
      type: String,
      default: null,
    },
    showClose: {
      type: Boolean,
      default: true,
    },
    mask: {
      type: Boolean,
      default: true,
    },
  },
  setup(props, { emit }) {
    // const visible = ref(false)

    const close = () => {
      emit('close')
    }

    const show = ref(props.visible)
    watchEffect(() => {
      if (props.visible) {
        show.value = true
      }
    })

    const modalStyle = computed(() => {
      return {
        width: props.width,
        height: props.height,
        perspective: '1300px',
        // backgroundImage: `url(${bgImage})`,
      }
    })

    const afterLeave = () => {
      show.value = false

      emit('closed')
    }

    return {
      // visible,
      close,
      modalStyle,
      show,
      afterLeave,
    }
  },
})
