
import { defineComponent } from 'vue'
import { useInjector } from '@/store/hook'
import { GameStateStore } from '@/store/hooks/game-info'
import { Dialog } from 'vant'
import { delay, showMessage } from '@/assets'
export default defineComponent({
  setup() {
    const store = useInjector(GameStateStore)
    if (!store) throw new Error('GameStateStore')
    const show = (playerId: number) => {
      const player = store.gameState.players.find(t => t.id === playerId)
      return new Promise(resolve => {
        if (!player) throw new Error('')

        if (player.isAI) {
          showMessage(`${player.name}`)
          delay(1).then(() => {
            resolve(true)
          })
        } else {
          Dialog.alert({
            title: '',
            message: `${player.name}!`,
          }).then(() => {
            resolve(true)
          })
        }
      })
    }
    return {
      show,
    }
  },
  render() {
    return null
  },
})
