
import { defineComponent, ref } from 'vue'

export default defineComponent({
  setup() {
    const visible = ref(false)
    const num = ref(1)
    let tick = 0

    const close = () => {
      tick = 0
      visible.value = false
    }

    const show = (val: number, closeDelay = 8000) => {
      if (tick) {
        clearTimeout(tick)
        tick = 0
      }
      num.value = val
      visible.value = true

      tick = setTimeout(close, closeDelay)
    }

    return {
      visible,
      num,
      show,
      close,
    }
  },
})
