
import { computed, defineComponent, ref } from 'vue'
import { useInjector } from '@/store/hook'
import { GameStateStore } from '@/store/hooks/game-info'
import { PersonType } from '@/assets/types'
import { confirm, showMessage } from '@/assets/index'

export default defineComponent({
  // components: { Button },
  setup() {
    const store = useInjector(GameStateStore)
    if (!store) throw new Error('GameStateStore')
    let selectPlayerId = 0

    const visible = ref(false)
    const currentPlayerId = ref(1) // id
    let resolveFun: (playerId: number) => void

    const selectPlayers = computed(() => {
      const players = store.gameState.players.filter(t => {
        return t.id !== currentPlayerId.value
      })

      return players
    })

    const show = (playerId: number) => {
      selectPlayerId = 0 // id
      currentPlayerId.value = playerId // id
      visible.value = true
      return new Promise<number>(resolve => {
        resolveFun = resolve
      })
    }

    const clickHandle = async (player: PersonType) => {
      // resolveFun(playerId)
      if (player.win) {
        showMessage(`${player.name},`)
        return false
      }

      if (player.map === 1) {
        showMessage(`${player.name},`)
        return false
      }

      const isok = await confirm(`${player.name}?`)

      if (isok) {
        selectPlayerId = player.id
        visible.value = false
      }

      return true
    }

    const cancelSelect = () => {
      selectPlayerId = 0
      visible.value = false
      // resolveFun(0)
    }

    const afterLeave = () => {
      resolveFun && resolveFun(selectPlayerId)
    }

    const statusText = (player: PersonType) => {
      if (player.win) {
        return `${player.win}`
      } else if (player.map === 1) {
        return ''
      } else {
        return ''
      }
    }

    return {
      visible,
      show,
      clickHandle,
      cancelSelect,
      afterLeave,
      selectPlayers,
      statusText,
    }
  },
})
