import mitt from 'mitt'

export const bus = mitt()

export const ACTION = {
  WINDOW_RESIZE: 'window resize', // 
  RENDER: 'render', // 
  WIN: 'win',
  CHANGE_POSITION: 'change position', //  {player:PersonType,map:0|1,index:number}
  ADD_LEVEL: 'add level', //  { index }
  APPEND_MESSAGE: 'append message', // {message:string,player:PersonType}
}

window.addEventListener('resize', () => {
  const width = document.body.offsetWidth
  const height = document.body.offsetHeight

  bus.emit(ACTION.WINDOW_RESIZE, { width, height })
})

window.addEventListener('orientationchange', () => {
  const width = document.body.offsetWidth
  const height = document.body.offsetHeight
  bus.emit(ACTION.WINDOW_RESIZE, { width, height, orientation: window.orientation })
})
